.h-clients {
  --v-h-client-title-padding-x: 2.3333rem;
}
.h-clients__title {
  padding: var(--v-h-client-title-padding-x) 0;
}

.clients-slider {
  --v-clients-slider-border-radius: 5px;
  position: relative;
}
.clients-slider__content {
  overflow: hidden;
}
.clients-slider__item {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 140px;
  background-color: var(--v-dark);
  border-radius: var(--v-clients-slider-border-radius);
}
.clients-slider .swiper-button-disabled {
  background-color: var(--v-gray-600);
}
.clients-slider .swiper-button-disabled:hover {
  background-color: var(--v-gray-600);
}